import './App.css';
import { useQuery, gql } from '@apollo/client';
import { useState, useEffect} from 'react';

const ALL_DEPARTING_TRAINS_TO_HELSINKI =  gql`
    query trains {
      stop(id: "HSL:1392551") {
        name
        stoptimesWithoutPatterns {
          realtimeArrival
          serviceDay
          trip {
            route {
              gtfsId,
              shortName
            }
          }
        }
      }
    }`;

function Trains({train_departures}) {
  console.log('Trains called');

  return <div>
    {train_departures.map((td, ix) => (
      <span className='indent' key={ix}>
      <p><span className="train_sign">{td[0]}</span><span>&nbsp;&nbsp;&nbsp;{td[1]} min</span></p></span>
  ))}</div>;
}


function App() {

  const { loading, error, data, startPolling, stopPolling} = useQuery(ALL_DEPARTING_TRAINS_TO_HELSINKI);
  
  const [time, setTime] = useState(Date.now());

  useEffect(() => {
    const interval = setInterval(() => setTime(Date.now()), 10000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  if (loading) return 'Loading...';
  if (error) return `Error! ${error.message}`;


  console.log('App component redraw');

  const current_date = Date.now();

  var train_departures= [];
  data.stop.stoptimesWithoutPatterns.forEach(stwp => {
    const dT = Math.round((new Date((stwp.realtimeArrival + stwp.serviceDay)*1000) - current_date) / 1000 / 60);
    if (dT > 0) {
      train_departures.push([stwp.trip.route.shortName, dT]);
    }
  });

  return (
    <div className="App">
      <header className="App-header">
        <h1>Aikaa junan lhtn</h1>
        <Trains train_departures={train_departures}/>
      </header>
    </div>
  );

}

export default App;
